body {
  font-family: "Raleway";
  color: #272829;
  background:rgb(0, 0, 0);
  background-size: cover;
}
ul {
  list-style-type: none;
}
h1, h2, h3, h4, h5, h6, button {
  font-family: 'Poppins';
}
html {
  scrollbar-color: var(--color1) var(--color2);
  scrollbar-width: thin;
}
html::-webkit-scrollbar {
  width: 8px;     /* Tamaño del scroll en vertical */
  height: 8px;    /* Tamaño del scroll en horizontal */
  background-color: var(--color2);
}

/* Ponemos un color de fondo y redondeamos las esquinas del thumb */
html::-webkit-scrollbar-thumb {
  background: var(--color1);
  border-radius: 4px;
}
/* Cambiamos el fondo y agregamos una sombra cuando esté en hover */
html::-webkit-scrollbar-thumb:hover {
  background: #11c9b7;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}
/* Cambiamos el fondo cuando esté en active */
html::-webkit-scrollbar-thumb:active {
  background-color: #059385;
}

:root {
  --color1: #14f0da;
  --color2: #040b14;
  --color3: #fff;
  --color4: #212431;
  --color5: rgb(8, 160, 150);
  --color6: #e01078;
  --color7: #e60b5b;
}

/* ---------------------------------------------------
# Sidebar
--------------------------------------------------- */
.App-sidebar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 290px;
  transition: all ease-in-out 0.5s;
  z-index: 9997;
  transition: all 0.5s;
  padding: 0 15px;
  background: var(--color2);
  overflow-y: auto;
  opacity: 0.9;
}

/* Sidebar's Header styles */
#sidebar .profile img {
  margin: 45px auto;
  display: block;
  width: 90px;
  height: 90px;
  border: 6px solid var(--color5); 
  background: #428bca;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#sidebar .profile h1 {
  font-size: 24px;
  margin: 0;
  padding: 0;
  font-weight: 600;
  -moz-text-align-last: center;
  text-align-last: center;
}

#sidebar .profile h1 a, #sidebar .profile h1 a:hover {
  color: var(--color3);
  text-decoration: none;
}

#sidebar .profile .social-links a {
  font-size: 18px;
  display: inline-block;
  background: var(--color4);
  color: var(--color1);
  line-height: 1;
  padding: 10px 4px;
  margin-right: 7px;
  border-radius: 50%;
  text-align: center;
  width: 39px;
  height: 39px;
  transition: 0.3s;
}

#sidebar .profile .social-links a:hover {
  background: var(--color1);
  color: var(--color3);
  text-decoration: none;
  padding: 12px 6px;
  width: 42px;
  height: 42px;
}

#main {
  margin-left: 290px;
  background-color: var(--color4);
  color: var(--color3);
}

@media (max-width: 1199px) {
  #sidebar {
    left: -300px;
  }
  #main {
    margin-left: 0;
  }
}

/*--------------------------------------------------------------
# Sidebar Navigation Menu
--------------------------------------------------------------*/
/* Desktop Navigation */
.nav-menu {
  padding: 30px 10px 0 0;
}

.nav-menu * {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  border-radius: 4px;
}

.nav-menu > ul > li {
  position: relative;
  white-space: nowrap;
}

.nav-menu a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a8a9b4;
  padding: 12px 15px;
  margin-bottom: 8px;
  transition: 0.3s;
  font-size: 15px;
}

.nav-menu a i {
  font-size: 24px;
  padding-right: 8px;
  color: rgb(8, 160, 150);
  flex: 2;
}

.nav-menu span {
  transition: 0.1s;
  flex: 10;
}

.nav-menu a:hover, .nav-menu .active, .nav-menu li:hover > a {
  text-decoration: none;
  color: var(--color3);
  background-color: rgb(91, 82, 82);
  margin-left: 5px;
}

.nav-menu a:hover i, .nav-menu .active i, .nav-menu li:hover > a i {
  color: var(--color1);
}

/* Mobile Navigation */
.mobile-nav-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  z-index: 9998;
  border: 0;
  font-size: 26px;
  transition: all 0.3s;
  outline: none !important;
  color: var(--color6);
  width: 40px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
  border-radius: 50px;
  cursor: pointer;
  border: 2px solid var(--color1);
}

.mobile-nav-active #sidebar {
  left: 0;
}

/*--------------------------------------------------------------
# Home Section
--------------------------------------------------------------*/
#home {
  width: 100%;
  height: 100vh;
  background: url("https://s1.1zoom.me/big0/42/Switzerland_Forests_494929.jpg") top center;
  background-size: cover;
}

#home:before {
  content: "";
  background: rgba(5, 13, 24, 0.3);
  position: absolute;
  min-height: 100vh;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

#home .home-container {
  position: relative;
  z-index: 2;
  min-width: 300px;
}

#home h1 {
  margin: 0 0 10px 0;
  font-size: 64px;
  font-weight: 700;
  line-height: 56px;
  color: var(--color3);
}

#home p {
  color: var(--color1);
  margin-bottom: 50px;
  font-size: 26px;
  font-family: "Raleway";
}

#home p span {
  color: var(--color3);
  padding-bottom: 4px;
  letter-spacing: 1px;
  border-bottom: 3px solid var(--color1);
}

@media (min-width: 1024px) {
  #home {
    background-attachment: fixed;
  }
}

@media (max-width: 768px) {
  #home h1 {
    font-size: 28px;
    line-height: 36px;
  }
  #home h2 {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 30px;
  }
}

/*--------------------------------------------------------------
# Sections General
--------------------------------------------------------------*/
section {
  padding: 60px 20px;
  overflow: hidden;
}
section .box {
  padding: 30px;
  width: 100%;
  box-shadow: 0 0 24px 0 rgb(24, 103, 115);
  background: #1f202a;
  border-radius: 24px;
}

.section-bg {
  background: #f5f8fd;
}

.section-title {
  padding-bottom: 30px;
}

.section-title h2 {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 20px;
  padding-bottom: 20px;
  position: relative;
  color: var(--color1);
  text-transform: uppercase;
  /*
  border-bottom: 4px solid var(--color6);
  border-bottom-left-radius: 50px 20px;
  border-bottom-right-radius: 50px 20px;
  */
}

/* Unused */
.section-title h2::after {
  content: '';
  display: inline-block;
  width: 60px;
  height: 3px;
  margin: 6px 13px;
  background: var(--color6);
  bottom: 0;
  left: 0;
}

.section-title p {
  margin-bottom: 0;
}

section h3 {
  font-weight: 700;
  font-size: 26px;
  color: var(--color5);
}

/*--------------------------------------------------------------
# About
--------------------------------------------------------------*/

#about .content ul {
  list-style: none;
  padding: 0;
}

#about .content ul li {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

#about .content ul strong {
  margin-right: 10px;
}

#about .content ul i {
  font-size: 16px;
  margin-right: 5px;
  color: var(--color6);
  line-height: 0;
}

#about .content p:last-child {
  margin-bottom: 0;
}

/*--------------------------------------------------------------
# Projects
--------------------------------------------------------------*/
#projects .projects-title {
  font-size: 26px;
  font-weight: 700;
  margin-top: 20px;
  margin-bottom: 20px;
  color: var(--color5);
}

#projects .projects-item {
  padding: 0 0 20px 20px;
  margin-top: -2px;
  border-left: 2px solid var(--color6);
  position: relative;
}

#projects .projects-item h4 {
  line-height: 18px;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--color5);
  margin-bottom: 10px;
}

#projects .projects-item h5 {
  font-size: 16px;
  color: var(--color2);
  background: var(--color1);
  padding: 5px 15px;
  display: inline-block;
  font-weight: 600;
  margin-bottom: 10px;
}

#projects .projects-item ul {
  padding-left: 20px;
}

#projects .projects-item ul li {
  padding-bottom: 10px;
}

#projects .projects-item:last-child {
  padding-bottom: 0;
}

#projects .projects-item::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50px;
  left: -9px;
  top: 0;
  background: #fff;
  border: 2px solid var(--color6);
}

#projects .project-tools ul {
  padding-top: 15px;
}
#projects .project-tools i {
  color: var(--color6);
}
#projects .badge-keyword {
  font-family: 'Poppins';
  font-size: 9px;
  background-color: var(--color6);
  padding: 3px 3px;
  border-radius: 3px;
}

/*--------------------------------------------------------------
# Skills
--------------------------------------------------------------*/
.skills .progress {
  height: 60px;
  display: block;
  background: none;
  border-radius: 0;
}

.skills .progress .skill {
  padding: 0;
  margin: 0 0 6px 0;
  text-transform: uppercase;
  display: block;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: #ffffff;
}

.skills .progress .skill .val {
  float: right;
  font-style: normal;
}

.skills .progress-bar-wrap {
  background: var(--color1);
  height: 10px;
}

/*--------------------------------------------------------------
# Contact
--------------------------------------------------------------*/
.contact {
  padding-bottom: 130px;
}

.contact .info {
  padding: 30px;
  width: 100%;
  box-shadow: 0 0 24px 0 rgb(24, 22, 22);
  background: var(--color4);
  border-radius: 24px;
}

.contact .contact-form {
  width: 100%;
  padding: 30px;
}

.contact .contact-form .form-group {
  padding-bottom: 8px;
  margin-bottom: 15px;
}

.contact .contact-form .sent-message {
  color: rgb(43, 93, 172);
  background: #18d26e;
  text-align: center;
  padding: 15px;
  font-weight: 600;
}

.contact .contact-form .loading {
  background: var(--color6);
  text-align: center;
  padding: 15px;
}

.contact .contact-form .loading:before {
  content: "";
  display: inline-block;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: 0 10px -6px 0;
  border: 3px solid #18d26e;
  border-top-color: rgb(255, 25, 25);
  -webkit-animation: animate-loading 1s linear infinite;
  animation: animate-loading 1s linear infinite;
}

.contact .contact-form label {
  padding-bottom: 8px;
}

.contact .contact-form input, .contact .contact-form textarea {
  border-radius: 8px;
  box-shadow: none;
  font-size: 14px;
  background: var(--color4);
  color: var(--color3);
}

.contact .contact-form input {
  height: 44px;
}

.contact .contact-form textarea {
  padding: 10px 15px;
}

.contact .contact-form button {
  background: var(--color7);
  border: solid 1px var(--color3);
  padding: 10px 24px;
  color: var(--color3);
  transition: 0.4s;
  border-radius: 4px;
}

.contact .contact-form button:hover {
  background: var(--color1);
  border: solid 1px var(--color6);
  color: var(--color4);
}

@-webkit-keyframes animate-loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes animate-loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}